<template>
  <div class="infoManage">
    <div class="infoManage-title">
      <span class="infoManage-title-slashBefor">汽车管理</span>
      <span class="infoManage-title-slash">/</span>
      <span class="infoManage-title-slashAfter">汽车信息管理</span>
    </div>

    <!-- 表单 -->
    <div class="infoManage-form">
      <el-form :inline="true" :model="formInline" class="demo-form-inline">
        <el-form-item>
          <el-input v-model="formInline.name" placeholder="汽车名称"></el-input>
        </el-form-item>

        <el-form-item>
          <el-input v-model="formInline.brand" placeholder="汽车品牌"></el-input>
        </el-form-item>

        <el-form-item>
          <el-select v-model="formInline.category" placeholder="汽车类别">
            <el-option
              v-for="(item, index) in carCategory"
              :key="index"
              :label="item.title"
              :value="item.category"
            ></el-option>
          </el-select>
        </el-form-item>

        <el-form-item>
          <el-button type="primary" @click="onSearch" icon="el-icon-search">查询</el-button>
        </el-form-item>

        <el-form-item>
          <el-button type="success" @click="openAddDialog" icon="el-icon-plus">新增</el-button>
        </el-form-item>
      </el-form>
    </div>

    <!-- 表格 -->
    <div class="infoManage-table">
      <el-table
        height="400"
        :data="pageList"
        :header-cell-style="{background:'rgb(238, 241, 246)',color:'rgb(96, 98, 102)'}"
      >
        <el-table-column label="序号" type="index" width="50" align="center" header-align="center"></el-table-column>
        <el-table-column prop="name" label="汽车名称" width="120" align="center" header-align="center"></el-table-column>
        <el-table-column prop="brand" label="品牌" align="center" header-align="center">
          <template slot-scope="scope">
            <span v-if="scope.row.brand"> {{scope.row.brand}} </span>
            <span v-else>未知</span>
          </template>
        </el-table-column>
        <el-table-column label="类别" width="100" align="center" header-align="center">
          <template slot-scope="scope">
            <span v-if="scope.row.category == 'car'">轿车</span>
            <span v-else-if="scope.row.category == 'suv'">SUV</span>
            <span v-else-if="scope.row.category == 'mpv'">MPV</span>
            <span v-else-if="scope.row.category == 'sportscar'">跑车</span>
            <span v-else-if="scope.row.category == 'pickup'">皮卡</span>
            <span v-else-if="scope.row.category == 'minivan'">微面</span>
            <span v-else-if="scope.row.category == 'miniup'">微卡</span>
            <span v-else-if="scope.row.category == 'lightbus'">轻客</span>
            <span v-else>未知</span>
          </template>
        </el-table-column>
        <el-table-column prop="engine" label="发动机" align="center" header-align="center">
          <template slot-scope="scope">
            <span v-if="scope.row.engine"> {{scope.row.engine}} </span>
            <span v-else>未知</span>
          </template>
        </el-table-column>
        <el-table-column prop="gearbox" label="变速箱" width="120" align="center" header-align="center">
          <template slot-scope="scope">
            <span v-if="scope.row.gearbox"> {{scope.row.gearbox}} </span>
            <span v-else>未知</span>
          </template>
        </el-table-column>
        <el-table-column label="驱动方式" align="center" header-align="center">
          <template slot-scope="scope">{{ scope.row.drivingMode == 0 ? "两驱" : "四驱" }}</template>
        </el-table-column>
        <el-table-column label="能源" align="center" header-align="center">
          <template slot-scope="scope">
            <span v-if="scope.row.energy == 0">汽油</span>
            <span v-else-if="scope.row.energy == 1">柴油</span>
            <span v-else-if="scope.row.energy == 2">纯电动</span>
            <span v-else>油电混合</span>
          </template>
        </el-table-column>
        <el-table-column prop="seatNumber" label="座位数" align="center" header-align="center"></el-table-column>
        <el-table-column prop="yearStyle" label="年代款" align="center" header-align="center">
          <template slot-scope="scope">
            <span v-if="scope.row.yearStyle"> {{scope.row.yearStyle}} </span>
            <span v-else>未知</span>
          </template>
        </el-table-column>
        <el-table-column prop="price" label="售价" align="center" header-align="center"></el-table-column>
        <el-table-column fixed="right" label="操作" width="200" align="center" header-align="center">
          <template slot-scope="scope">
            <el-button size="small" @click="openEditBox(scope.row)" icon="el-icon-edit">编辑</el-button>
            <el-button
              @click="openDeleteBox(scope.row)"
              type="danger"
              size="small"
              icon="el-icon-delete"
            >删除</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <!-- 分页 -->
    <div class="infoManage-pagination">
      <el-pagination
        @current-change="handleCurrentChange"
        :current-page.sync="currentPage"
        :page-size="pageSize"
        layout="total, prev, pager, next"
        :total="totalNumber"
      ></el-pagination>
    </div>

    <!-- 新增 -->
    <div class="infoManage-eadd">
      <el-dialog
        title="新增"
        :visible.sync="addDialog"
        width="50%"
        center
        :before-close="closeAddBox"
      >
        <div class="infoManage-add-form">
          <el-form
            :model="addForm"
            status-icon
            :rules="rules"
            ref="addForm"
            label-width="100px"
            class="demo-addForm"
          >
            <el-row :gutter="20">
              <el-col :span="12">
                <el-form-item label="汽车名称" prop="name">
                  <el-input v-model.trim="addForm.name" autocomplete="off"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="12">
                <el-form-item label="品牌" prop="brand">
                  <el-input v-model="addForm.brand" autocomplete="off"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="12">
                <el-form-item label="类别" prop="category">
                  <el-select v-model="addForm.category">
                    <el-option label="轿车" value="car"></el-option>
                    <el-option label="SUV" value="suv"></el-option>
                    <el-option label="MPV" value="mpv"></el-option>
                    <el-option label="跑车" value="sportscar"></el-option>
                    <el-option label="皮卡" value="pickup"></el-option>
                    <el-option label="微面" value="miniup"></el-option>
                    <el-option label="轻客" value="lightbus"></el-option>
                  </el-select>
                </el-form-item>
              </el-col>
              <el-col :span="12">
                <el-form-item label="发动机" prop="engine">
                  <el-input v-model="addForm.engine"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="12">
                <el-form-item label="变速箱" prop="gearbox">
                  <el-input v-model="addForm.gearbox"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="12">
                <el-form-item label="驱动方式" prop="drivingMode">
                  <el-radio v-model="addForm.drivingMode" :label="0">两驱</el-radio>
                  <el-radio v-model="addForm.drivingMode" :label="1">四驱</el-radio>
                </el-form-item>
              </el-col>
              <el-col :span="12">
                <el-form-item label="能源" prop="energy">
                  <el-select v-model="addForm.energy">
                    <el-option label="汽油" value="0"></el-option>
                    <el-option label="柴油" value="1"></el-option>
                    <el-option label="纯电动" value="2"></el-option>
                    <el-option label="油电混合" value="3"></el-option>
                  </el-select>
                </el-form-item>
              </el-col>
              <el-col :span="12" prop="seatNumber">
                <el-form-item label="座位数">
                  <el-input-number v-model="addForm.seatNumber" :min="1" :max="10"></el-input-number>
                </el-form-item>
              </el-col>
              <el-col :span="12">
                <el-form-item label="年代款" prop="yearStyle">
                  <el-input v-model="addForm.yearStyle"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="12">
                <el-form-item label="售价" prop="price">
                  <el-input-number :controls="false" v-model.number="addForm.price"></el-input-number>
                </el-form-item>
              </el-col>
            </el-row>
          </el-form>
        </div>
        <span slot="footer" class="dialog-footer">
          <el-button @click="closeAddBox">取 消</el-button>
          <el-button type="primary" @click="isAdd(addForm)">添 加</el-button>
        </span>
      </el-dialog>
    </div>

    <!-- 编辑框 -->
    <div class="infoManage-edit">
      <el-dialog
        title="编辑"
        :visible.sync="editDialog"
        width="50%"
        center
        :before-close="closeEditBox"
      >
        <div class="infoManage-edit-form">
          <el-form
            :model="editForm"
            status-icon
            ref="editForm"
            label-width="100px"
            class="demo-editForm"
          >
            <el-row :gutter="20">
              <el-col :span="12">
                <el-form-item label="汽车名称" prop="name">
                  <el-input v-model="editForm.name" autocomplete="off"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="12">
                <el-form-item label="品牌" prop="brand">
                  <el-input v-model="editForm.brand" autocomplete="off"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="12">
                <el-form-item label="类别" prop="category">
                  <el-select v-model="editForm.category">
                    <el-option label="轿车" value="car"></el-option>
                    <el-option label="SUV" value="suv"></el-option>
                    <el-option label="MPV" value="mpv"></el-option>
                    <el-option label="跑车" value="sportscar"></el-option>
                    <el-option label="皮卡" value="pickup"></el-option>
                    <el-option label="微面" value="miniup"></el-option>
                    <el-option label="轻客" value="lightbus"></el-option>
                  </el-select>
                </el-form-item>
              </el-col>
              <el-col :span="12">
                <el-form-item label="发动机" prop="engine">
                  <el-input v-model="editForm.engine"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="12">
                <el-form-item label="变速箱" prop="gearbox">
                  <el-input v-model="editForm.gearbox"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="12">
                <el-form-item label="驱动方式" prop="drivingMode">
                  <el-radio v-model="editForm.drivingMode" :label="0">两驱</el-radio>
                  <el-radio v-model="editForm.drivingMode" :label="1">四驱</el-radio>
                </el-form-item>
              </el-col>
              <el-col :span="12">
                <el-form-item label="能源" prop="energy">
                  <el-select v-model="editForm.energy">
                    <el-option label="汽油" :value="0"></el-option>
                    <el-option label="柴油" value="1"></el-option>
                    <el-option label="纯电动" value="2"></el-option>
                    <el-option label="油电混合" value="3"></el-option>
                  </el-select>
                </el-form-item>
              </el-col>
              <el-col :span="12">
                <el-form-item label="座位数">
                  <el-input-number v-model="editForm.seatNumber" :min="1" :max="10"></el-input-number>
                </el-form-item>
              </el-col>
              <el-col :span="12">
                <el-form-item label="年代款" prop="yearStyle">
                  <el-input v-model="editForm.yearStyle"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="12">
                <el-form-item label="售价" prop="price">
                  <el-input-number :controls="false" v-model.number="editForm.price"></el-input-number>
                </el-form-item>
              </el-col>
            </el-row>
          </el-form>
        </div>
        <span slot="footer" class="dialog-footer">
          <el-button @click="closeEditBox">取 消</el-button>
          <el-button type="primary" @click="isConfirm">确 定</el-button>
        </span>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import { carInfo } from "../api/";

export default {
  name: "InformationManagement",
  data() {
    return {
      // 表单
      formInline: {
        name: "",
        brand: "",
        category: ""
      },
      // 新增表单
      addForm: {
        name: "",
        brand: "",
        category: "",
        engine: "",
        gearbox: "",
        drivingMode: 0,
        energy: "",
        seatNumber: 5,
        yearStyle: "",
        price: 10
      },
      editForm: {}, // 编辑表单
      carCategory: [
        { title: "全部", category: "all" },
        { title: "轿车", category: "car" },
        { title: "SUV", category: "suv" },
        { title: "MPV", category: "mpv" },
        { title: "跑车", category: "sportscar" },
        { title: "皮卡", category: "pickup" },
        { title: "微面", category: "minivan" },
        { title: "微卡", category: "miniup" },
        { title: "轻客", category: "lightbus" }
      ],
      tableData: [],
      tableList: [],
      pageList: [],
      totalNumber: 0,
      addDialog: false,
      editDialog: false,
      currentPage: 1,
      pageSize: 10,
      rules: {
        name: { required: true, message: "请输入汽车名称", trigger: "blur" }
      }
    };
  },
  computed: {
    isCollapse() {
      return this.$store.state.isCollapse;
    }
  },
  created() {
    carInfo
      .then(res => {
        this.tableData = res.data.data;
        this.totalNumber = this.tableData.length;
      })
      .catch(function(error) {
        return error;
      });
  },
  watch: {
    tableData () {
      this.onSearch()
    }
  },
  methods: {
    // 查询
    onSearch () {
      const name = this.formInline.name; // 汽车名称
      const brand = this.formInline.brand; // 汽车品牌
      const category = this.formInline.category; // 汽车类别
      const searchList = this.tableData.filter( value => {
        if (category && category != "all" && value.category != category) {
          return false
        }
        return true
      });
      const filterList = searchList.filter( value => {
        if (brand && value.brand != brand) {
          return false
        }
        return true
      });

      const finalList = filterList.filter( value => {
        // 模糊查询
        if (name && !value.name.includes(name)) {
          return false
        }
        return true
      });
      this.pageList = finalList.filter((item, index) => index < this.pageSize );
      this.totalNumber = finalList.length;
    },


    // 分页
    handleCurrentChange(val) {
      this.pageList = this.tableData.filter((item, index) => index < this.pageSize * val && index >= this.pageSize * (val - 1))
      this.currentPage = val
    },

    // 新增
    //打开新增对话框
    openAddDialog() {
      this.addDialog = !this.addDialog;
    },
    // 关闭新增对话框
    closeAddBox() {
      this.addDialog = !this.addDialog;
    },
    // 确认添加吗
    isAdd() {
      this.$refs["addForm"].validate(valid => {
        if (valid) {
          this.$confirm("确认添加吗？")
            .then(_ => {
              let form = JSON.parse(JSON.stringify(this.addForm));
              this.tableData.unshift(form);
              this.closeAddBox();
              this.$message({
                type: "success",
                message: "添加成功!"
              });
            })
            .then(() => {
              this.$refs["addForm"].resetFields();
            })
            .catch(_ => {
              this.closeAddBox();
            });
        } else return false;
      });
    },

    // 编辑
    // 打开编辑框
    openEditBox(row) {
      this.editDialog = !this.editDialog;
      this.editForm = row;
    },
    // 关闭编辑框
    closeEditBox() {
      this.editDialog = !this.editDialog;
    },
    // 确认提交修改吗
    isConfirm(done) {
      this.$confirm("确认提交吗？")
        .then(_ => {
          done();
        })
        .catch(_ => {
          this.closeEditBox();
        });
    },

    // 删除
    // 打开删除框
    openDeleteBox(row) {
      this.$confirm("确认删除该记录吗?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(() => {
        this.handleClick(row);
        this.$message({
          type: "success",
          message: "删除成功!"
        });
      });
    },
    // 删除元素
    handleClick(row) {
      const id = row.id;
      const deleteValue = this.tableData.filter(row => id == row.id);
      this.tableData.splice(this.tableData.indexOf(deleteValue[0]), 1);
    }
  }
};
</script>

<style scoped>
.infoManage {
  padding: 20px;
  padding-top: 10px;
}
/* 标题 */
.infoManage-title {
  padding-bottom: 10px;
}
.infoManage-title-slashAfter,
.infoManage-title-slashBefor {
  font-size: 16px;
  color: #606266;
}
.infoManage-title-slashAfter {
  font-weight: bold;
}
.infoManage-title-slash {
  margin: 0 5px;
  font-size: 16px;
  font-weight: bold;
  color: #c0c4cc;
}
/* 表单 */
.infoManage-form {
  padding: 10px;
  position: relative;
  background-color: #f4f4f4;
}
.el-form-item {
  margin-bottom: 0;
}
/* 表格 */
.infoManage-table {
  margin: 10px 0;
}
/* 分页 */
.infoManage-pagination {
  text-align: center;
  background-color: #f4f4f4;
}
/* 新增框 */
.infoManage-eadd .el-col {
  margin: 10px 0;
}
/* 编辑框 */
.el-col {
  margin: 8px 0;
}
</style>
<style>
.is-without-controls .el-input__inner{
  text-align: left;
}
</style>


